import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Select, Form, DatePicker } from 'antd';
import moment from 'moment';
import { zones } from 'dump/data';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    name: '',
  };
  return { ...initialValueDefault, ...initialValue };
};
const { Option } = Select;
const dateFormat = 'YYYY/MM/DD';
@Form.create({ name: 'FormCreateSubject' })
class FormCreateSubject extends Component {
  state = {
    initialValue: {},
  };

  componentDidMount = () => {
    // const { match } = this.props;
    // if (match.params.id) {
    //   this.setState({ zoneId: match.params.id });
    //   this.getZoneById(match.params.id);
    // }
  };

  getZoneById = code => {
    const zoneMap = zones.filter(zone => zone.code === code)[0];
    const initialValue = handleInitialValue(zoneMap);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card title="Thêm đợt đăng lý">
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Đợt" {...formItemLayout}>
              {getFieldDecorator('code', {
                initialValue: initialValue.code,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  <Option value="1">1</Option>
                  <Option value="2">2</Option>
                  <Option value="3">3</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item hasFeedback label="Học kỳ" {...formItemLayout}>
              {getFieldDecorator('code1', {
                initialValue: initialValue.code,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  <Option value="1">1</Option>
                  <Option value="2">2</Option>
                  <Option value="3">3</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item hasFeedback label="Năm học" {...formItemLayout}>
              {getFieldDecorator('code2', {
                initialValue: initialValue.code,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  <Option value="1">2015-2016</Option>
                  <Option value="2">2016-2017</Option>
                  <Option value="3">2017-2018</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item hasFeedback label="Ngày bắt đầu" {...formItemLayout}>
              {getFieldDecorator('code3', {
                initialValue: initialValue.code,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <DatePicker defaultValue={moment('2015/01/01', dateFormat)} format={dateFormat} />
              )}
            </Form.Item>
            <Form.Item hasFeedback label="Ngày kết thúc" {...formItemLayout}>
              {getFieldDecorator('code3', {
                initialValue: initialValue.code,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <DatePicker defaultValue={moment('2015/01/01', dateFormat)} format={dateFormat} />
              )}
            </Form.Item>

            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormCreateSubject {...props} />
  </Page>
));



